<template>
  <view>
    
      
      <view >
         <view class="" style="height:90vh;border-radius:50px;margin:0 20px;">
             <view class="center" style="height: 90px;font-size:30px;">
              <view class="" style="margin-top: 30px;">
                 活动展示页面
              </view>
             </view>
             
             <view class="">
               <view class="column"  >
                 <view class="center margin_10" >
                   <view class="space-between width_active" style="">
                     <view class="font_size">
                       <text>主题:</text>
                     </view>
                     <view class="">
                       {{theme}}
                     </view>
                     
                   </view>
                 </view>
                 <view class="center margin_10">
                   <view class="space-between width_active" style="">
                     <view class="font_size">
                       <text>时间:</text>
                     </view>
                     <view class="">
                       {{time}}
                     </view>
                     
                   </view>
                 </view>
                 <view class="center margin_10">
                   <view class="space-between " style="width: 220px;">
                     <view class="font_size">
                       <text>要求:</text>
                     </view>
                     <view class="">
                       {{request}}
                     </view>
                     
                   </view>
                 </view>
                 <view class="center margin_10">
                   <view class="space-between " style="width: 220px;">
                     <view class="font_size">
                       <text>联系电话:</text>
                     </view>
                     <view class="">
                       {{phone}}
                     </view>
                     
                   </view>
                 </view>
                 <view class="center margin_10">
                   <view class="space-between " style="width: 220px;">
                     <view class="font_size">
                       <text>联系人:</text>
                     </view>
                     <view class="">
                       {{people}}
                     </view>
                     
                   </view>
                 </view>
                 <view class="center margin_10">
                   <view class="space-between " style="width: 220px;">
                     <view class="font_size">
                       <text>截止日期:</text>
                     </view>
                     <view class="">
                       {{stop_date}}
                     </view>
                     
                   </view>
                 </view>
               </view>
             </view>
           
           <view class="" style="margin:40px 20px;">
             <u-button type="primary" text="我要报名" shape="circle" @click="tobaoming"></u-button>
           </view>
           
         
           
           
           
           
         </view>
        
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
         
          
      </view>
    
    
    
    	
    
    
    
    
    
    
    
    
    
    
    
    
    
    <view class="">
      <u-modal :show="show" content="" @confirm="confirm(shanchu)"  height="300px" ref="uModal" :asyncClose="true" showCancelButton @cancel="cancel" >
        <view class="">
          是否删除这条信息？
        </view>
      </u-modal>
    </view> 
    
    
    
  </view>
</template>

<script>
  export default {
    data() {
      return {
        
          show: false,
          theme:'',
          time:'',
          request:'',
          phone:'',
          people:'',
          stop_date:''
          
          
      };
    },
    onLoad() {
  
  let value = uni.getStorageSync('active')
  this.theme=value.theme
  this.time=value.time
  this.request=value.request
  this.phone=value.phone
  this.people=value.people
  this.stop_date=value.stop_date
  console.log(value.address)
  
  
  
  
  
  
  
  
    		},
        methods:{
          tobaoming(){
             
           
            uni.navigateTo({
              url:'../../pages/baoming/baoming'
            })
          },
          tologoin(){
            uni.navigateTo({
              url:"/uni_modules/uni-id-pages/pages/login/login-withpwd"
            })
          },
          
          
          baojiadetail(e){
            console.log(e)
            uni.setStorageSync('baojiabiao', e)
            uni.navigateTo({
              url:'/pages/baojiabiaoxiangqing/baojiabiaoxiangqing'
            })
          },
          showModal() {
          			this.show = true;
          		},
          baojiadel(item) {
                this.shanchu=item
          			this.showModal()
          		},
            
            
            
            async confirm(item) {
              const db = uniCloud.database();
              const collection = db.collection('baojia') // 获取表对象
              const res = await collection.doc(item._id).remove() // 根据id删除记录
              console.log(item._id)
              uni.reLaunch({
                url:'../../pages_self/baojiabiao/baojiabiao'
              })
            },
           cancel(){
             uni.switchTab({
               url:'../../pages_self/index/index'
             })
           },
          
          
          
          
        }
  }
</script>

